/* 在全局 CSS 文件中添加 */
.ant-input-number-handler-wrap {
  opacity: 1 !important;
  visibility: visible !important;
}

.ant-input-number-handler-up,
.ant-input-number-handler-down {
  pointer-events: auto !important;
}

.draggable {
  cursor: move; /* 当鼠标悬浮时，显示拖拽光标 */
}

.card-container {
  margin-bottom: 100px;
}

/* 以下是design相关的样式 */
.custom-panel-header {
  font-weight: bold; /* 加粗字体 */
  color: #050404;
  background-color: #fff;
}

/* 覆盖 antd Panel 的默认内边距，减小高度 */
.ant-collapse > .ant-collapse-item > .ant-collapse-header {
  padding-top: 5px;
  padding-bottom: 5px;
}

/* 如果你有一个特定的类用于整个 Collapse 组件，也可以这样做 */
.custom-collapse .ant-collapse > .ant-collapse-item > .ant-collapse-header {
  padding-top: 4px;
  padding-bottom: 4px;
}

/* 调整 Panel 的内容区域内边距 */
.ant-collapse-content > .ant-collapse-content-box {
  padding: 8px;
}

/* 覆盖Ant Design Collapse组件的默认圆角样式 */
.ant-collapse > .ant-collapse-item > .ant-collapse-header,
.ant-collapse > .ant-collapse-item .ant-collapse-content {
  border-radius: 0 !important; /* 使用!important来确保覆盖任何默认样式 */
}

